import React, { useRef } from 'react'
import { IoIosArrowDown } from "react-icons/io";
import ReactFullpage from '@fullpage/react-fullpage';
import { useWindowSize } from 'react-use';

import Box from '../../components/Box'
import Flex from '../../components/Flex'
import Text from '../../components/Text'
import Button from '../../components/Button'
import Link from '../../components/Link'
import BackgroundImage from '../../components/BackgroundImage'
// import FullpageVerticalCenter from '../../components/FullpageVerticalCenter'
import { Media, responsive } from '../../contexts/responsive';

import bg from './bg.png'
import title from './title.png'
import Layout from '../Layout';
import Intro from './Intro';

const anchors = ["", "intro", "thirdPage"];

const IndexPage = () => {
  const fpApi = useRef()
  return (
    <Layout
      getFullpageApi={() => fpApi.current}
    >
      <ReactFullpage
        anchors={anchors}
        //fullpage options
        licenseKey = {process.env.FULLPAGE_JS_KEY}
        scrollingSpeed = {1000} /* Options here */
        render={({ state, fullpageApi}) => {
          fpApi.current = fullpageApi
          return (
            <ReactFullpage.Wrapper>
              <Box.Relative className="section">
                <Box.Absolute top="0" left="0" right="0">
                  <BackgroundImage src={bg} height="100vh" ratio={1920 / 1080} />
                </Box.Absolute>
                <Box.Absolute top="11.85%" left="8.7%" width="45.15%">
                  <BackgroundImage src={title} ratio={1735 / 961} />
                </Box.Absolute>
                <Box.Absolute left="11%" bottom="12%">
                  <Button
                    onClick={() => fullpageApi.moveSectionDown()}
                    fontSize={responsive('1em', '2.25em')}
                    p={responsive('0.5em 1.25em 0.5em 1em', '0.5625rem 2.875rem 0.5625rem 2.25rem')}
                    iconSpacing="0"
                    leftIcon={<IoIosArrowDown size="1.5em" />}
                  >
                    往下滑查看完整介紹
                  </Button>
                </Box.Absolute>
              </Box.Relative>
              <Intro fullpageApi={fullpageApi} isActive={fullpageApi?.getActiveSection().index == 1} />
            </ReactFullpage.Wrapper>
          );
        }}
      />
    </Layout>
  )
}

export default IndexPage
