import React, { useEffect, useMemo, useRef, useState } from 'react'
import {
  Modal,
  ModalContent,
  ModalCloseButton,
  useDisclosure,
  Divider,
  Portal,
} from "@chakra-ui/react"
import { isArray } from 'lodash';
import { FaInfoCircle } from "react-icons/fa";
import ReactHoverObserver from 'react-hover-observer';
import { navigate } from 'gatsby';

import Box from '../../components/Box'
import Text from '../../components/Text'
import Flex from '../../components/Flex'
import Button from '../../components/Button'
import BackgroundImage from '../../components/BackgroundImage'
import Animation from '../../components/Animation'
import { responsive } from '../../contexts/responsive'

import { animals } from '../data';

import bg_far from './bg_far.png'

// import { headerHeight } from '../../contexts/header/Provider'

const labels = ['身長', '特徵', '生物角色', '捕捉目的']
const headingTrans = {
  left: '80vw',
  right: '-80vw',
}

const Animal = ({ d, show, index }) => {
  const transitionDelay = useMemo(() => Math.random() / 2, [])
  const isFish = d.species === '魚類'
  const pos = {
    top: d.top,
    bottom: d.bottom,
    left: d.left,
    right: d.right,
    width: d.width,
  }
  return (
    <ReactHoverObserver>
      {({ isHovering }) => (
        <Box.Absolute
          opacity={show ? 0 : 1}
          style={pos}
          transform={`translateX(${show && isFish ? headingTrans[d.heading] : 0})`}
          transition="opacity 1.5s, transform 2s"
          transitionTimingFunction="cubic-bezier(0.250, 0.460, 0.450, 0.940)"
          transitionDelay={`${transitionDelay}s`}
          onClick={() => navigate('/guide')}
        >
          {isFish ? (
            <Animation delay={`${index * -0.5}s`}>
              <BackgroundImage title={d.name} src={d.src} ratio={d.ratio} />
            </Animation>
          ) : <BackgroundImage title={d.name} src={d.src} ratio={d.ratio} />}
          {isHovering && (
            <Portal>
              <Box.Fixed style={pos}>
                <Box.Absolute
                  zIndex={99}
                  borderRadius="1.5rem"
                  p="1em"
                  width={responsive('50%', '18em')}
                  bg="white"
                  top="15%"
                  left="95%"
                  {...d.info}
                >
                  <Text.Bold whiteSpace="pre-wrap" fontSize={responsive('1em', '2em')}>{d.name}</Text.Bold>
                  <Text whiteSpace="pre-wrap" fontSize={responsive('1em', '1.25em')}>{d.eng}</Text>
                  {d.data && d.data.map((data, k) => (
                    <Box key={k}>
                      <Divider mt="0.25em" mb="0.375em" borderBottom="1px solid" opacity="1" />
                      <Flex alignItems="center">
                        <Box width="5em">{labels[k]}</Box>
                        <Flex alignItems="center" flex={1}>
                          {isArray(data.icon) ? data.icon.map((icon, key) => (
                            <Flex alignItems="center" ml={key && '0.25em'} key={key}>
                              <Box color="black" width="1.875em" mr="0.25rem">
                                {icon}
                              </Box>
                              <Text.Bold fontSize={responsive('1em', '1.125em')}>{data.text[key]}</Text.Bold>
                            </Flex>
                          )) : data.icon && (
                            <Box color="black" width="1.875em" mr="0.25rem">
                              {data.icon}
                            </Box>
                          )}
                          {!isArray(data.text) && <Text.Bold fontSize={responsive('1em', '1.125em')}>{data.text}</Text.Bold>}
                        </Flex>
                      </Flex>
                    </Box>
                  ))}
                </Box.Absolute>
              </Box.Fixed>
            </Portal>
          )}
        </Box.Absolute>
      )}
    </ReactHoverObserver>
  )
}

const Intro = ({ fullpageApi, isActive }) => {
  const hasOpened = useRef()
  const [first, setStatus] = useState(true)
  const { isOpen, onOpen, onClose } = useDisclosure()
  useEffect(() => {
    if (isActive && first) {
      setTimeout(() => onOpen(), 1000)
    }
  }, [isActive])

  useEffect(() => {
    if (isOpen) {
      hasOpened.current = true
      fullpageApi?.setAllowScrolling(false)
    } else {
      if (hasOpened.current) setStatus(false)
      fullpageApi?.setAllowScrolling(true)
    }
  }, [isOpen])
  return (
    <Box className="section">
      <Box.Absolute top="0" left="0" right="0">
        <BackgroundImage src={bg_far} height="100vh" ratio={1920 / 1080} />
      </Box.Absolute>
      <Box.Absolute display={first ? 'none' : 'block'} left="4.5em" top="7.875em">
        <Button
          px="1em"
          leftIcon={<FaInfoCircle />}
          onClick={() => {
            onOpen(true)
          }}
        >打開指標生物與底質介紹</Button>
      </Box.Absolute>
      {animals.map((d, i) => (
        <Animal d={d} index={i} show={first} key={i} />
      ))}
      <Modal
        autoFocus={false}
        isCentered
        size="3xl"
        isOpen={isOpen}
        onClose={onClose}
      >
        <ModalContent
          px={responsive('2em', '4.75em')}
          pt={responsive('2.3125em', '5.625em')}
          pb={responsive('1.5em', '3.75em')}
          bg="linear-gradient(rgba(255, 255, 255, 100%) 50%, rgba(255,255,255,0.4))"
          borderRadius="1.5em"
        >
          <ModalCloseButton
            color="custom.lightGray"
            fontSize="2.5em"
            right="2.25rem"
            top="2.25rem"
            _hover={{ bg: 'transparent' }}
            _active={{ bg: 'transparent' }}
          />
          <Text lineHeight="1" textAlign="center" color="custom.titleBlack" fontSize="2.25em">為什麼要認識指標生物與底質？</Text>
          <Box mt="2em" pt="2em" borderTop="1px solid" borderColor="">
            <Text color="custom.gray">下方挑選出的指標性生物是由美國珊瑚礁體檢基金會(Reef Check Foundation)創辦人 Gregor Hudgson 參考許多文獻及調查方法後，精選出在印度太平洋區域廣泛分佈的物種。</Text>
            <Text.Bold>而被選擇的原因是由於牠們在珊瑚礁生態扮演重要與關鍵的角色，其族群數量的多寡不僅可反映當地的珊瑚礁生態健康與否，甚至反映出人為活動對礁區的影響。</Text.Bold>
            <Text color="custom.gray"><Text.Inline color="black" fontWeight="bold">但重要的是，這些指標性生物的多寡對於珊瑚礁生態沒有絕對的好或不好</Text.Inline>，因為珊瑚礁生態是屬於一個動態平衡，需要全盤檢視與它連動的生物、非生物因子，並且有長期監測數據作為判斷基礎，才能瞭解該指標性生物的族群數量對當地礁區是過多或過少。</Text>
          </Box>
        </ModalContent>
      </Modal>
    </Box>
  )
}

export default Intro
